import React, { Component } from "react";
import { Card, Button, Row, Container, Col } from "react-bootstrap";
import { Redirect } from "react-router-dom";
import runtimeEnv from "@mars/heroku-js-runtime-env";
const env = runtimeEnv();
console.log(env.REACT_APP_URL);
console.log(window.location.host);
export default class Home extends Component {
  state = {
    redirect: false,
    to: "",
  };
  setRedirect = (url) => {
    this.setState({
      redirect: true,
      to: url,
    });
  };
  renderRedirect = () => {

    if (this.state.redirect) {
      return <Redirect to={this.state.to} />;
    }
  };
  componentDidMount() {
    const favicon = document.getElementById("FavIcon");
    if (
      env.REACT_APP_FAVICONURL !== undefined ||
      env.REACT_APP_FAVICONURL !== ""
    )
      favicon.href = `${env.REACT_APP_FAVICONURL}`;
    const title = document.getElementById("AppTitle");
    if (env.REACT_APP_TITLE !== undefined || env.REACT_APP_TITLE !== "")
      title.innerText = `${env.REACT_APP_TITLE}`;
    else {
      title.innerText = "Media Login Panel";
    }
  }
  render() {
    return (
      <div>
        {this.renderRedirect()}
        <Card
          style={{
            position: "absolute",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
          }}
          className="text-center"
        >
          <Card.Header>Select a login option</Card.Header>
          <Card.Body>
            <Container fluid>
              <Col>
                {
                  env.REACT_APP_INSTAGRAM === "true" ?
                    <Row>
                      <Button
                        style={{ width: "100%", margin: "5px", padding: "5px" }}
                        size="lg"
                        variant="outline-danger"
                        onClick={() => this.setRedirect("/Instagram")}
                      >
                        INSTAGRAM
                    </Button>
                    </Row> : ""
                }
                {
                  env.REACT_APP_TWITTER === "true" ?
                    <Row>
                      <Button
                        style={{ width: "100%", margin: "5px", padding: "5px" }}
                        size="lg"
                        variant="outline-primary"
                        onClick={() => this.setRedirect("/Twitter")}
                      >
                        TWITTER
                      </Button>
                    </Row> : ""
                }
                {
                  env.REACT_APP_LINKEDIN === "true" ?
                    <Row>
                      <Button
                        style={{ width: "100%", margin: "5px", padding: "5px" }}
                        size="lg"
                        variant="outline-info"
                        onClick={() => this.setRedirect("/LinkedIn")}
                      >
                        LINKEDIN
                       </Button>
                    </Row> : ""
                }
                {
                  env.REACT_APP_PINTEREST === "true" ?
                    <Row>
                      <Button
                        style={{ width: "100%", margin: "5px", padding: "5px" }}
                        size="lg"
                        variant="outline-secondary"
                        onClick={() => this.setRedirect("/Pinterest")}
                      >
                        Pinterest
                       </Button>
                    </Row> : ""
                }
                {
                  env.REACT_APP_FACEBOOK === "true" ?
                    <Row>
                      <Button
                        style={{ width: "100%", margin: "5px", padding: "5px" }}
                        size="lg"
                        variant="outline-primary"
                        onClick={() => this.setRedirect("/Facebook")}
                      >
                        Facebook
                       </Button>
                    </Row> : ""
                }

              </Col>
            </Container>
          </Card.Body>
        </Card>
        <Card title="Select login options"></Card>
      </div>
    );
  }
}
